 <template>
    <div class="index fullpage-container">
        <div class="fullpage-wp" v-fullpage="opts" ref="example">
            <!-- banner广告 -->
            <div class="bannerWrap page-1 page" v-if='showBanner'>
                <div class="bannerLoop">
                    <swiper :options="bannerOption" ref="bannerOption" class="clear">
                        <swiper-slide class="item"  v-for="(item,index) in bannerLoop" key="index">
                            <div class="bg" :style="{backgroundImage:'url('+ item.SlideImage +')'}"></div>
                            <div class="txt" v-show = 'item.SlideIntro'>
                                <div class="tit clear">
                                    <h4>{{ item.SlideTitle }}</h4>
                                    <div class="tips" style="display:block;">
                                        <p style="width:100%;">{{item.SlideTitlePlus}}</p>
                                    </div>
                                </div>
                                <div class="en">
                                    <p>{{ item.SlideIntro }}</p>
                                </div>
                            </div>
                        </swiper-slide>
                        <div class="swiper-button-prev btn prev" slot="button-prev"></div>
                <div class="swiper-button-next btn next" slot="button-next"></div>
                    </swiper>
                </div>
            </div>
            <loading v-if="!showBanner"></loading>
            <!-- 解决方案 -->
            <div class="part solution clearfix page-2 page">
                <div class="title" :class="{ active : scroll >= baseFont * ( 30 - 5 ) }">
                    <p>MyeHR服务模式</p>
                    <span class="line"></span>
                    <!-- <p class="desc">{{solutionBnCn.ModuleNamePlus}}</p> -->
                </div>
                <div class="cont" :class="{ active : scroll >= baseFont * ( 30 - 5 ) }" ref="slideBox">
                    <swiper :options="solutionOption" ref="solutionSwiper" class="clear">
                        <swiper-slide class="item" v-for="(item,index) in solution" :key='item.PlanPath'>
                            <router-link :to="{path: item.PlanPath}" v-if='index % 2'>
                                <div class="txt">
                                    <!-- <i class="icon iconfont" :class='item.PlanIcon'></i> -->
                                    <p class="tit">{{ item.PlanTitle }}</p>
                                    <ul>
                                        <li v-for="cell in item.PlanTitlePlus">{{cell}}</li>
                                    </ul>
                                </div>
                                <div class="img clear">
                                    <a href="javascript:"  :style="{backgroundImage:'url(\'static/'+item.PlanThumb+'\')'}"></a>
                                    <span class="arrow"></span>
                                    <div class="shadow">
                                        <span class="icon"></span>
                                    </div>
                                </div>
                            </router-link>
                            <router-link :to="{path: item.PlanPath}"  v-else>
                                <div class="img clear">
                                    <a href="javascript:"  :style="{backgroundImage:'url(\'static/'+item.PlanThumb+'\')'}"></a>
                                    <span class="arrow"></span>
                                    <div class="shadow">
                                        <span class="icon"></span>
                                    </div>
                                </div>
                                <div class="txt">
                                    <!-- <i class="icon iconfont" :class='item.PlanIcon'></i> -->
                                    <p class="tit">{{ item.PlanTitle }}</p>
                                    <ul>
                                        <li v-for="cell in item.PlanTitlePlus">{{cell}}</li>
                                    </ul>
                                </div>
                                <div class="shadow">
                                </div>
                            </router-link>
                        </swiper-slide>
                        <div class="swiper-button-prev btn prev" slot="button-prev"></div>
                        <div class="swiper-button-next btn next" slot="button-next"></div>
                    </swiper>
                    <div class="more">
                        <router-link to="/solution">查看更多</router-link>
                    </div>
                </div>
            </div>
            <!-- 产品与服务 -->
            <div class="part product page-3 page">
                <div class="title" :class="{ active : scroll >= baseFont * ( 66 - 5 ) }">
                    <p>关于我们</p>
                    <span class="line"></span>
                </div>
                <div class="cont" :class="{ active : scroll >= baseFont * ( 30 - 5 ) }" ref="aboutus">
                    <div class="iconwarp clear">
                     <div class="iconitem iconitem1" v-animate="{value: 'bounceInLeft', delay: 0}">
                       <img src="/static/img/icon_1.png" alt="">
                       <h2>600万个人用户<br/>8千家企业用户</h2>
                       <p>HR用户体验，积累丰富的行业经验</p>
                     </div>
                     <div class="iconitem iconitem2" v-animate="{value: 'bounceInLeft', delay: 600}">
                       <img src="/static/img/icon_2.png" alt="">
                       <h2>18年行业耕耘</h2>
                       <p>专业人力资源管理信息化软件服务</p>
                     </div>
                     <div class="iconitem iconitem3" v-animate="{value: 'bounceInLeft', delay: 1200}">
                       <img src="/static/img/icon_3.png" alt="">
                       <h2>22家全国性分支机构</h2>
                       <p>国内专业eHR专业厂商最强</p>
                     </div>
                     <div class="iconitem iconitem4" v-animate="{value: 'bounceInLeft', delay: 1800}">
                       <img src="/static/img/icon_4.png" alt="">
                       <h2>450名专业实施团队</h2>
                       <p>包含咨询顾问、项目经理、软件工程师等专业团队</p>
                     </div>
                   </div>
                </div>
            </div>
            <!-- 我们的客户 -->
            <div class="part client case clear page-4 page">
                <div class="title" :class="{ active : scroll >= baseFont * ( 30 - 5 ) }">
                    <p>典型客户</p>
                    <span class="line"></span>
                    <!-- <p class="desc">{{solutionBnCn.ModuleNamePlus}}</p> -->
                </div>
                <div class="case_list" >
                    <div class="title">
                        <ul class="clear" ref="ullist">
                            <!-- <li :class="{ active : currentType === 0}" type-id='0' @click="clickType(-1,0)"><span>全部</span></li> -->
                            <li :type-id='item.CaseTypeId' v-for="(item,index) in allCaseDate" @click="clickType(index,item.CaseTypeId)" :class="{ active : currentType === (index + 1)}">
                                <span>{{ item.CaseTypeTitle }}</span>
                            </li>
                        </ul>
                    </div>
                    <div class="cont">
                        <loading v-show="!showDetail"></loading>
                        <ul class="clear list" v-show='showDetail'>
                            <li class="item" v-for="(item,index) in showCaseDate" :style="{backgroundImage:'url(\'static/'+item.ThumbPicture+'\')'}"></li>
                        </ul>
                    </div>
                </div>
            </div>

            <v-foot class="page-5 page"></v-foot>
        </div>
    </div>
</template>
<script type="text/javascript">
    import $ from 'jquery';
    import axios from 'axios';
    import foot from './../foot.vue';
    import loading from './../loading.vue';
    import swiper from './../swiper.vue';
    import swiperSlide from './../slide.vue';
    const baseUrl = '';
    export default{
        data() {
            return {
                head: {},
                scroll: '',
                itemWidth: '',
                poster: '',
                next: 0,
                solution: {},
                product: {},
                solutionBnCn: {},
                proBnCn: {},
                showSol: true,
                showSolBn: true,
                showBanner: false,
                dark: false,
                plus: [],
                bannerUrl: baseUrl,
                base_url: baseUrl,
                clientBg: '',
                phone: null,
                bannerLoop: {},
                showDetail: false,
                allCaseDate: {},
                showCaseDate: {},
                allCaseLogo: [],
                currentType: 1,
                solutionOption: {
                    //  所有配置均为可选（同Swiper配置）
                    autoplay: false,
                    setWrapperSize: true,
                    autoHeight: true,
                    paginationClickable: true,
                    observeParents: true,
                    onlyExternal: true,
                    slidesPerView: 4,
                    nextButton: '.solution .swiper-button-next',
                    prevButton: '.solution .swiper-button-prev',
                    speed: 1000,
                    breakpoints: {
                        768: {
                            slidesPerView: 2,
                            onlyExternal: false
                        },
                        1024: {
                            slidesPerView: 3,
                            onlyExternal: false
                        }
                    }
                },
                bannerOption: {
                    //  所有配置均为可选（同Swiper配置）
                    autoplay: false,
                    loop: true,
                    setWrapperSize: true,
                    autoHeight: true,
                    paginationClickable: true,
                    observeParents: true,
                    onlyExternal: true,
                    slidesPerView: 1,
                    nextButton: '.bannerLoop .swiper-button-next',
                    prevButton: '.bannerLoop .swiper-button-prev',
                    speed: 1000
                },
                opts: {
                    start: 0,
                    dir: 'v',
                    duration: 500
                }
            };
        },
        computed: {
            webType() {
                if ((navigator.userAgent.indexOf('MSIE') >= 0) && (navigator.userAgent.indexOf('Opera') < 0)) {
                    return 1;
                } else {
                    return 0;
                }
            },
            baseFont() {
                return parseInt($('html').css('font-size'));
            },
            cacl() {
                this.itemWidth = $('div.index div.solution.part div.cont div.slideBox div.bd').width() * 0.25;
            },
            swiper() {
                return this.$refs.solutionSwiper.swiper;
            }
        },
        components: {
            'loading': loading,
            'vFoot': foot,
            'swiper': swiper,
            'swiperSlide': swiperSlide
        },
        methods: {
            menu() {
                this.scroll = document.body.scrollTop || document.documentElement.scrollTop;
            },
            moveNext() {
                this.$refs.example.$fullpage.moveNext(); // Move to the next page
            },
            nextSolution() {
                let len = $(this.$refs.solutionSwiper).find('.item').length - 5;
                let oWidth = $(this.$refs.solutionSwiper).width() / 4;
                let oLeft = parseInt($(this.$refs.solutionSwiper).find('.list').css('left'));
                if (oWidth * len >= Math.abs(parseInt($(this.$refs.slideBox).find('.list').css('left')))) {
                    $(this.$refs.slideBox).find('.list').stop().animate({
                        'left': oLeft - oWidth + 'px'},
                    600);
                } else {
                    $(this.$refs.slideBox).find('.list').stop().animate({
                        'left': '0px'},
                    600);
                }
            },
            prevSolution() {
                // let len = $(this.$refs.slideBox).find('.item').length - 5;
                let oWidth = $(this.$refs.slideBox).find('.bd').width() / 4;
                let oLeft = parseInt($(this.$refs.slideBox).find('.list').css('left'));
                if (parseInt($(this.$refs.slideBox).find('.list').css('left')) < 0) {
                    $(this.$refs.slideBox).find('.list').stop().animate({
                        'left': oLeft + oWidth + 'px'},
                    600);
                } else {
                    $(this.$refs.slideBox).find('.list').stop().animate({
                        'left': '0px'},
                    600);
                }
            },
            initial() {
                if ($(window).width() >= 768) {
                    // 设置banner的高度
                    $('div.index div.banner').height($('div.index div.banner').width() * 0.5);
                    $('div.index div.bannerLoop').height($('div.index div.bannerLoop').width() * 0.5);
                    // 设置解决方案的高度
                    $(this.$refs.slideBox).find('.swiper-container').height($(this.$refs.slideBox).find('.swiper-container').width() * 0.4375);
                    // 产品与服务图片高度设置
                    $('div.index div.part.product div.cont div.tp div.item a div.img').height($('div.index div.part.product div.cont div.tp div.item a div.img').width() * 0.8);
                    // 设置北调服务高度设置
                    $('div.index div.part.product div.cont div.btm').height($('div.index div.part.product div.cont div.btm').width() * 0.1354);
                    // 我们的客户图片高度设置
                    $('div.index div.part.client div.ad').height($('div.index div.part.client div.ad').width() * 0.34375);
                } else {
                    // 设置banner的高度
                    $('div.index div.banner').height($('div.index div.banner').width() * 0.875);
                    $('div.index div.bannerLoop').height($('div.index div.bannerLoop').width() * 0.875);
                    // 设置解决方案的高度
                    $(this.$refs.slideBox).find('.swiper-container').height($(this.$refs.slideBox).find('.swiper-container').width() * 0.875);
                    // 产品与服务图片高度设置
                    $('div.index div.part.product div.cont div.tp div.item a div.img').height($('div.index div.part.product div.cont div.tp div.item a div.img').width() * 0.87);
                    // 设置北调服务高度设置
                    $('div.index div.part.product div.cont div.btm').height($('div.index div.part.product div.cont div.btm').width() * 0.34375);
                    // 我们的客户图片高度设置
                    $('div.index div.part.client div.ad').height($('div.index div.part.client div.ad').width() * 0.703125);
                }

                // 设置解决方案各个部分的宽度
                $(this.$refs.slideBox).find('.item').width($(this.$refs.slideBox).find('.bd').width() / 4);
            },
            rerender() {
                this.initial();
            },
            getDate() {
                let that = this;
                // 获取banner数据设置
                this.showBanner = false;
                // 解决方案设置
                this.showSol = false;
                this.showSolBn = false;
                axios.get('./../../static/data/index-body.json').then((response) => {
                    // console.log(response);
                    let data = response.data;
                    that.bannerUrl = data.base_url;
                    // 获取baner数据
                    that.bannerLoop = data.head.slide;
                    // console.log(that.bannerLoop);
                    that.showBanner = true;
                    // 获取解决方案数据
                    // 解决方案标题
                    that.solutionBnCn = data.plan_intro;
                    that.showSolBn = true;
                    // 解决方案各个模块
                    that.solution = data.plan;
                    // console.log(data.plan);
                    that.showSol = true;
                    // 获取产品与服务数据
                    // 产品与服务标题
                    that.proBnCn = data.product_intro;
                });
            },
            getCaseList() {
                this.showDetail = false;
                axios.get('./../../static/data/case.json').then((response) => {
                    // console.log(response.data.content);
                    this.allCaseDate = response.data.content;
                    let content = [];
                    let resData = response.data.content;
                    for (let i = 0; i < resData.length; i++) {
                        for (let j = 0; j < resData[i].caseinfo.length; j++) {
                            content.push({ThumbPicture: resData[i].caseinfo[j].ThumbPicture});
                        }
                    }
                    this.allCaseLogo = content;
                    this.showCaseDate = content.slice(0, 18);
                    console.log(content);
                    this.showDetail = true;
                });
            },
            clickType: function (index, type) {
                this.currentType = index + 1;
                if (index < 0) {
                    this.showCaseDate = this.allCaseLogo;
                } else {
                    let content = [];
                    for (var i = 0; i < this.allCaseDate.length; i++) {
                        if (this.allCaseDate[i].CaseTypeId === type) {
                            for (let j = 0; j < this.allCaseDate[i].caseinfo.length; j++) {
                                content.push({ThumbPicture: this.allCaseDate[i].caseinfo[j].ThumbPicture});
                            }
                        }
                    }
                    this.showCaseDate = content;
                }
            }
        },
        mounted() {
            window.addEventListener('scroll', this.menu);
            window.addEventListener('resize', this.rerender);
            this.initial();
        },
        created() {
            this.getDate();
            this.getCaseList();
        }
    };
</script>
<style type="text/css">
.fullpage-container{position:absolute;top:0;left:0;width:100%;height:100%;}
.page{top:2em;}
div.index{width:100%;}
div.index .swiper-button-disabled{pointer-events:auto;}
div.index div.banner{position:relative;overflow:hidden;width:100%;height:46rem;background:rgba(0,0,0,.4);}
div.index div.banner div.text{position:absolute;top:50%;left:50%;display:inline-block;vertical-align:top;text-align:center;transform:translate(-50%,-50%);}
div.index div.banner div.text div.tit{text-align:center;}
div.index div.banner div.text div.tit h4{display:inline-block;color:#fff;vertical-align:top;font-weight:400;font-size:3.5rem;line-height:4.6rem;}
div.index div.banner div.text div.tit span.line{display:block;display:inline-block;margin-top:1.4rem;margin-right:2.5rem;margin-left:2.5rem;width:.15rem;height:1.8rem;background:#fff;vertical-align:top;}
div.index div.banner div.text div.tit div.tips{display:inline-block;vertical-align:top;}
div.index div.banner div.text div.tit div.tips p{color:#fff;font-size:1.3rem;line-height:2.3rem;}
div.index div.banner div.text div.tit div.tips p span{display:block;}
div.index div.banner div.text div.en{margin-top:3.5rem;color:#fff;font-size:2.3rem;line-height:1;}
div.index div.banner div.bg{position:absolute;top:0;left:0;z-index:-1;display:block;width:100%;height:100%;}
div.index div.banner div.next{position:absolute;bottom:3rem;left:50%;transform:translateX(-50%);}
div.index div.banner div.next i.iconfont{background:-moz-linear-gradient(top,#0fce98,#008cf5);background:-webkit-gradient(linear,0 0,0 bottom,from(#0fce98),to(#008cf5));background:-ms-linear-gradient(top,#0fce98,#008cf5);color:transparent;font-size:2rem;cursor:default;FILTER:progid:DXImageTransform.Microsoft.Gradient(startColorStr=#0fce98,endColorStr=#008cf5);-webkit-background-clip:text;-webkit-text-fill-color:transparent;}
div.index div.banner div.next i.nobg{background:0 0;color:#008cf5;}
div.index div.banner span.music{position:absolute;top:6rem;right:6rem;display:block;width:1.5rem;height:1.5rem;background:url(../../../static/img/on.png) no-repeat center center;background-size:contain;}
div.index div.banner span.music.active{background-image:url(../../../static/img/off.png);}
div.index div.bannerWrap{height:48rem;}
div.index div.bannerLoop{position:relative;overflow:hidden;width:100%;height:100%;background-size:cover;}
div.index div.bannerLoop div.swiper-container{width:100%;height:100%;}
div.index div.bannerLoop div.swiper-container div.swiper-wrapper{height:100%!important;}
div.index div.bannerLoop div.swiper-container div.swiper-wrapper div.item{position:relative;height:100%;}
div.index div.bannerLoop div.swiper-container div.swiper-wrapper div.item div.bg{display:block;width:100%;height:100%;background-size:cover;}
div.index div.bannerLoop div.swiper-container div.swiper-wrapper div.item div.bg:before{display:block;width:100%;height:100%;background:rgba(0,0,0,.4);content:'';}
div.index div.bannerLoop div.item{position:relative;}
div.index div.bannerLoop div.txt{position:absolute;top:50%;left:50%;display:inline-block;vertical-align:top;text-align:center;-webkit-transform:translate(-50%,-50%);-moz-transform:translate(-50%,-50%);-o-transform:translate(-50%,-50%);transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);}
div.index div.bannerLoop div.txt div.tit{text-align:center;}
div.index div.bannerLoop div.txt div.tit h4{display:inline-block;color:#fff;vertical-align:top;font-weight:400;font-size:2.8rem;line-height:4.6rem;}
div.index div.bannerLoop div.txt div.tit span.line{display:block;display:inline-block;margin-top:1.4rem;margin-right:2.5rem;margin-left:2.5rem;width:.15rem;height:1.8rem;background:#fff;vertical-align:top;}
div.index div.bannerLoop div.txt div.tit div.tips{display:inline-block;vertical-align:top;}
div.index div.bannerLoop div.txt div.tit div.tips p{color:#fff;font-size:1.3rem;line-height:2.3rem;}
div.index div.bannerLoop div.txt div.tit div.tips p span{display:block;}
div.index div.bannerLoop div.txt div.en{margin-top:3.5rem;color:#fff;font-size:2.3rem;line-height:1;}
div.index div.bannerLoop div.next{position:absolute;right:10px;bottom:3rem;-webkit-transform:translateX(-50%);-moz-transform:translateX(-50%);-o-transform:translateX(-50%);transform:translateX(-50%);-ms-transform:translateX(-50%);}
div.index div.bannerLoop div.next i.iconfont{background:-moz-linear-gradient(top,#0fce98,#008cf5);background:-webkit-gradient(linear,0 0,0 bottom,from(#0fce98),to(#008cf5));background:-ms-linear-gradient(top,#0fce98,#008cf5);color:transparent;font-size:2rem;cursor:default;FILTER:progid:DXImageTransform.Microsoft.Gradient(startColorStr=#0fce98,endColorStr=#008cf5);-webkit-background-clip:text;-webkit-text-fill-color:transparent;}
div.index div.bannerLoop div.next i.nobg{background:0 0;color:#008cf5;}
div.index div.part div.title{padding-top:1.3rem;padding-bottom:2.5rem;text-align:center;-webkit-transition:all 1s;-moz-transition:all 1s;-o-transition:all 1s;transition:all 1s;-webkit-transform:translateY(30%);-moz-transform:translateY(30%);-o-transform:translateY(30%);transform:translateY(30%);-ms-transform:translateY(30%);-ms-transition:all 1s;}
div.index div.part div.title p{color:#333;font-size:24px;line-height:4.3rem;}
div.index div.part div.title span.line{display:inline-block;margin:0 auto;width:4rem;height:.2rem;background:#0079ef;}
div.index div.part div.title p.desc{margin-top:1.6rem;color:#666;font-size:16px;line-height:1rem;}
div.index div.product.part div.title p{color:#fff;}
div.index div.product.part div.title span.line{background:#fff;}
.iconwarp{margin:0 auto;padding-top:120px;padding-bottom:60px;width:905px;}
.iconitem{position:relative;top:0;float:left;margin:13px;width:200px;height:200px;border:3px solid #83A7CB;border-radius:50%;text-align:center;opacity:1!important;}
.iconitem h2{margin:10px 0;color:#fff;}
.iconitem p{padding:0 27px;color:#fff;font-size:12px;}
.iconitem img{margin-top:20px;}
@media screen and (max-width:767px){div.index div.part div.title p{font-size:1.7rem;}
div.index div.part div.title p.desc{font-size:.8rem;}
}
div.index div.part div.title.active{-webkit-transform:translateY(0);-moz-transform:translateY(0);-o-transform:translateY(0);transform:translateY(0);-ms-transform:translateY(0);}
div.index div.part div.cont div.more{margin:3rem auto 5rem;width:10rem;height:2.8rem;border-radius:1.4rem;background:url(../../../static/img/more_border.png) no-repeat center center;background-size:contain;}
div.index div.part div.cont div.more:hover{background-image:url(../../../static/img/more_active.png);}
div.index div.part div.cont div.more:hover a{color:#fff;}
div.index div.part div.cont div.more a{display:block;width:100%;height:100%;color:#0079ef;text-align:center;font-size:16px;line-height:2.8rem;}
div.index div.solution.part div.cont{margin:0 auto;width:100%;max-width:1150px;-webkit-transition:all 1s .2s;-moz-transition:all 1s .2s;-o-transition:all 1s .2s;transition:all 1s .2s;-webkit-transform:translateY(3%);-moz-transform:translateY(3%);-o-transform:translateY(3%);transform:translateY(3%);-ms-transform:translateY(3%);-ms-transition:all 1s .2s;}
div.index div.solution.part div.cont div.swiper-container{position:relative;height:42rem;}
div.index div.solution.part div.cont div.swiper-container div.swiper-wrapper{height:100%!important;}
div.index div.solution.part div.cont div.swiper-container div.swiper-wrapper div.item{float:left;overflow:hidden;width:25vw;height:100%;}
