div.case div.case_list div.title1{margin-bottom:2rem;border-bottom:1px solid #e3e4e6;line-height:4.8rem;-webkit-tap-highlight-color:transparent;}
div.case div.case_list div.title1 div div{text-align:center;font-size:16px;}
div.case div.case_list div.title1 div div:after{float:right;color:#333;content:'|';}
div.case div.case_list div.title1 div div.swiper-slide-active{color:#0078f0;}
div.case div.case_list div.title1 div.active{color:#0078f0!important;}
div.case div.case_list div.cont{margin:0 auto;padding-right:0;padding-bottom:2.5rem;padding-left:0;width:90.625%;}
div.case div.case_list div.cont ul.list{width:100%;}
div.case div.case_list div.cont ul.list li{width:33.33333333%;height:6.4rem;line-height:6.4rem;}
}



</style>




